#todo {
  margin: 30px auto;
}

.item2 {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  border: 1px solid green;
}

#header {
  display: flex;
  flex-direction: row;
  align-items: center;
}

#header > h1 {
  flex: 1;
}

#header > button {
  margin-right: 20px;
  width: 100px;
  height: 50px;
  border-radius: 10px;
  color: white;
}

#Done {
  background-color: blue;
}

#Close {
  background-color: green;
}
